
import { EviewSelect } from "@/store/fileStore";
export default {
    // Компоненты
    // Аргументы
    props: {
        // Полный путь к файлу
        absolutePathFile: String,
        // Путь к символьному файлу
        pathLink: String,
    },
    data() {
        return {
            // Текст в `vcode`
            textFile: "",
        };
    },
    computed: {
        /* Какой виджет показывать, в зависимости от расширения файла */
        ViewSelect(): EviewSelect | undefined {
            if (this.pathLink) {
                let res = undefined;
                switch (this.pathLink.split(".").slice(-1)[0]) {
                    case "pdf": {
                        res = EviewSelect.pdf;
                        break;
                    }
                    case "png":
                    case "jpg": {
                        res = EviewSelect.img;
                        break;
                    }
                    case "txt": {
                        res = EviewSelect.text;
                        // Читать ссылку на файл при изменения пути `pathTxt`
                        fetch(this.pathLink)
                            .then((response) => response.text())
                            .then((text) => {
                                // @ts-ignore
                                this.textFile = text;
                            });
                        break;
                    }
                }
                return res;
            }
        },
    },
};
