
import { TFile } from "@/store/fileStore";

export default {
    emits: ["onClick"],
    // Компоненты
    components: {},
    // Аргументы
    props: {
        detail: {
            type: Object as () => TFile,
        },
        name: {
            type: String,
        },
    },
    // Методы
    methods: {
        onClick(event: any) {
            this.$emit("onClick", this.name, this.detail);
        },
    },
};
