
export default {
    emits: ["updatePath", "backPath"],
    // Компоненты
    // Аргументы
    props: {
        PathStr: String,
    },
    // Переменные
    data() {
        return {
            // ИмяПеременной: ЗначениеПоУмолчанию,
        };
    },
    // Методы
    methods: {
        backPath() {
            this.$emit("backPath");
        },
        updatePath(event) {
            this.$emit("updatePath", event.target.value);
        },
    },
};
