
export default {
    name:'vtextarea',

    props: {
        modelValue: String,
        placeholder: {
            type: String,
            default: () => "....",
        },
        background: {
            type: String,
            default: () => "#000",
        },
        color: {
            type: String,
            default: () => "#000",
        },
    },
    computed: {
        cssProps() {
            return {
                "--Фон": this.background,
                "--color": this.color,
            };
        },
    },
};
